@mixin hero-mockup {
  position: relative;
  height: $lem * 3;

  .figure {
    @include figure;
  }

  &--laptop {
    z-index: 1;
  }

  &--tablet {
    z-index: 2;

    .figure {
      width: 80%;
      margin: 0 auto;
    }
  }

  &--phone {
    z-index: 3;

    .figure {
      width: 80%;
      margin: 0 auto;
    }
  }

  @include hero-mockup--reposition;
}
